<template>
  <loading :active="isLoading"></loading>
  <div class="bg-light pt-5 pb-7">
    <div class="container">
      <div class="row justify-content-center flex-md-row flex-column-reverse">
        <div class="col-md-6">
          <div class="bg-white mb-5 shadow-lg">
            <div class="bg-tan text-white text-center">
              <h4 class="fw-bold p-2">填寫資料</h4>
            </div>
            <Form v-slot="{ errors }" @submit="createOrder" class="p-2">
              <div class="mb-3">
                <label for="email" class="form-label"><span class="text-danger">＊</span>Email</label>
                <Field
                  id="email"
                  name="email"
                  type="email"
                  class="form-control"
                  :class="{ 'is-invalid': errors['email'] }"
                  placeholder="請輸入 Email"
                  rules="email|required"
                  v-model="form.user.email"
                ></Field>
                <ErrorMessage
                  name="email"
                  class="invalid-feedback"
                ></ErrorMessage>
              </div>

              <div class="mb-3">
                <label for="name" class="form-label"><span class="text-danger">＊</span>收件人姓名</label>
                <Field
                  id="name"
                  name="姓名"
                  type="text"
                  class="form-control"
                  :class="{ 'is-invalid': errors['姓名'] }"
                  placeholder="請輸入姓名"
                  rules="required"
                  v-model="form.user.name"
                ></Field>
                <ErrorMessage
                  name="姓名"
                  class="invalid-feedback"
                ></ErrorMessage>
              </div>

              <div class="mb-3">
                <label for="tel" class="form-label"><span class="text-danger">＊</span>收件人電話</label>
                <Field
                  id="tel"
                  name="電話"
                  type="tel"
                  class="form-control"
                  :class="{ 'is-invalid': errors['電話'] }"
                  placeholder="請輸入電話"
                  rules="required"
                  v-model="form.user.tel"
                ></Field>
                <ErrorMessage
                  name="電話"
                  class="invalid-feedback"
                ></ErrorMessage>
              </div>

              <div class="mb-3">
                <label for="address" class="form-label"><span class="text-danger">＊</span>收件人地址</label>
                <Field
                  id="address"
                  name="地址"
                  type="text"
                  class="form-control"
                  :class="{ 'is-invalid': errors['地址'] }"
                  placeholder="請輸入地址"
                  rules="required"
                  v-model="form.user.address"
                ></Field>
                <ErrorMessage
                  name="地址"
                  class="invalid-feedback"
                ></ErrorMessage>
              </div>
              <div class="mb-3">
                <label for="payment" class="form-label"
                  ><span class="text-danger">＊</span>付款方式</label
                >
                <Field
                  class="form-select mb-3"
                  id="payment"
                  name="付款方式"
                  rules="required"
                  as="select"
                  :class="{ 'is-invalid': errors['付款方式'] }"
                  v-model="form.user.payment"
                >
                  <option value="">請選擇付款方式</option>
                  <option value="信用卡付款">信用卡付款</option>
                  <option value="ATM 轉帳">ATM 轉帳</option>
                  <option value="超商取貨付款">超商取貨付款</option>
                </Field>
                <ErrorMessage name="付款方式" class="invalid-feedback" />
              </div>
              <div class="mb-3">
                <label for="message" class="form-label">留言</label>
                <textarea
                  name=""
                  id="message"
                  class="form-control"
                  cols="30"
                  rows="5"
                  v-model="form.message"
                ></textarea>
              </div>
              <div class="d-flex justify-content-between">
                <router-link to="/cart" class="btn btn-outline-secondary">
                  上一步
                </router-link>
                <button class="btn btn-outline-brown">送出訂單</button>
              </div>
            </Form>
          </div>
        </div>
        <div class="col-md-6">
          <div class="border-0 shadow-lg mb-4 bg-white">
            <div class="bg-tan text-white text-center">
              <h4 class="mb-4 fw-bold p-2">購物清單</h4>
            </div>
            <div class="d-flex mx-2" v-for="item in carts">
              <img
                :src="item.product.imageUrl"
                alt=""
                class="me-2"
                style="width: 48px; height: 48px; object-fit: cover"
              />
              <div class="w-100">
                <div class="d-flex justify-content-between fw-bold">
                  <p class="mb-0">
                    {{ item.product.title }}
                  </p>
                  <p class="mb-0">x {{ item.qty }}</p>
                </div>
                <div class="d-flex justify-content-between">
                  <p class="text-muted mb-0">
                    <small
                      >NT$ {{ $filters.currency(item.product.price) }}</small
                    >
                  </p>
                  <p class="mb-0">NT${{ $filters.currency(item.total) }}</p>
                </div>
              </div>
            </div>
            <table class="table mt-4 border-top border-0 text-muted ">
              <tbody class="p-2">
                <tr>
                  <th class="border-0 pt-4">總計</th>
                  <td class="text-end border-0 pt-4">
                    NT${{ $filters.currency(cart.total) }}
                  </td>
                </tr>
                <tr >
                  <th class="border-0  text-danger">折扣價格</th>
                  <td class="text-end border-0  text-danger">
                    NT${{ $filters.currency(cart.total - cart.final_total) }}
                  </td>
                </tr>
              </tbody>
            </table>
            <div class="d-flex justify-content-between p-2 bg-tan text-white">
              <p class="h4 fw-bold ">總金額</p>
              <p class="h4 fw-bold">
                NT${{ $filters.currency(cart.final_total) }}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Toast from "@/utils/Toast";
export default {
  data() {
    return {
      cart: {},
      carts: [],
      isLoading: false,
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: "",
          payment: "WebATM",
        },
        message: "",
      },
      orderId: "",
    };
  },
  methods: {
    getCart() {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`;
      this.isLoading = true;
      this.$http.get(url).then((res) => {
        // console.log(res);
        this.cart = res.data.data;
        this.carts = res.data.data.carts;
        this.isLoading = false;
      });
    },
    createOrder() {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/order`;
      const order = this.form;
      this.isLoading = true;
      this.$http.post(url, { data: order }).then((res) => {
        console.log(res);
        this.orderId = res.data.orderId;
        // console.log(this.orderId);
        Toast.fire({
          icon: 'success',
          title: "訂單送出",
        });
        this.isLoading = false;
        this.$router.push(`/checkout/${this.orderId}`);
      });
    },
  },
  created() {
    this.getCart();
  },
};
</script>
